.detail__container {
  display: flex;
  flex-direction: column;
  max-width: 50%;
  align-items: center;
  justify-content: center;
  margin: auto;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 20px;
  border-radius: 10px;
  background-color: white;
  box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.detail__info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.detail__info .detail__title,
.detail__info .detail__year {
  margin: 10px;
  font-weight: 300;
}

.detail__info .detail__title {
  margin-bottom: 5px;
  font-size: 1.7rem;
  color: #2c2c2c;
}

.detail__info .detail__genres {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  margin: 5px 0px;
}

.detail__info li {
  margin-right: 10px;
  font-size: 1rem;
  font-weight: 300;
}

.detail__info .detail__summary {
  font-size: 1.2rem;
  font-weight: 300;
  line-height: 2rem;
}
